.btn{
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
  cursor: pointer;

  &:hover,
  &--hover{

  }

  &:active,
  &--active{

  }

  // Size modificator

  &-size-m {
    height: 45px;
    width: 212px;
    font-size: 18px;
    line-height: 45px;
  }

  // Color modificator

  &-orange{
    background: @color-primary;
    color: @color-text-light;
    transition: background .3s ease-out;
    &:hover,
    &--hover{
      background: @color-primary--hover;
    }

    &:active,
    &--active{

    }
  }

  &-ghost-orange {
    line-height: 36px;
    background: transparent;
    color: @color-primary;
    border: 2px solid @color-primary;
  }

  //

  &-white{
    background: #ffffff;
    color: #ffffff;
    transition: background .3s ease-out;
    &:hover,
    &--hover{
      background: #ffffff;
    }

    &:active,
    &--active{

    }
  }

  &-ghost-white {
    line-height: 36px;
    background: transparent;
    color: #ffffff;
    border: 2px solid #ffffff;
  }

  //

  &-yellow{
    background: #ffe300;
    color: #000;
    transition: background .3s ease-out;
    &:hover,
    &--hover{
      background: #ffffff;
    }

    &:active,
    &--active{

    }
  }

  &-ghost-yellow {
    line-height: 36px;
    background: transparent;
    color: #ffffff;
    border: 2px solid #ffffff;
  }

}
