footer,
.footer {
  &__container {
    padding: 15px 0;
    border-top: 1px solid #ebebeb;
  }
  //
  &__massanger {
    padding: 30px 0;
  }
  &__links {
    display: flex;
    @media (max-width: 414px) {
      justify-content: center;
      flex-wrap: wrap;
    }
    li {
      display: flex;
      align-items: center;
      list-style: none;
      font-size: 0.875em;

      &::after {
        content: '';
        display: block;
        height: 10px;
        width: 1px;
        background: @color-grey;
        margin: 0 15px;
        @media (max-width: 414px) {
          margin: 0 5px;
        }
      }

      &:last-child::after {
        display: none;
      }

      span {
        color: @color-grey;
      }

      @media (max-width: 414px) {
        &:nth-child(2)::after {
          display: none;
        }
        &:last-child {
          width: 100%;
          justify-content: center;
          margin-top: 10px;
          a {
            width: 100%;
            border: 2px solid #23b55d;
            border-radius: 25px;
            padding: 10px;
            text-align: center;
            text-decoration: none;
            color: #23b55d;
            font-size: 16px;
          }
        }
      }
      @media (max-width: 320px) {
        &:nth-child(1)::after {
          display: none;
        }
      }
    }
  }
  //
}
