
// Header
header,
.header {
  position: relative;
  width: 100%;
  padding: 25px 15px 25px;
  box-sizing: border-box;
  background-image: url('/img/background.jpg');
  background-position: center;
  background-size: cover;
//  background: #007aff;
  height: 400px;
  overflow: hidden;
  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
//    background: #0579ff;
  //  background: rgba(0, 0, 0, 0.2);
  }

  h1 {
    color: #fff;
    font-size: 3.160em;
    margin-bottom: 10px;
    .svg-key {
      display: none;
      position: relative;
      top: -13px;
      margin-left: 10px;
      opacity: 0.5;
      height: 30px;
    }
    @media(max-width: 414px) {
      font-size: 2em;
    }
  }
}

.work {
  font-size: 14px;
  line-height: 14px;
  margin-left: 15px;
  margin-top: 5px;
  color: #fff;
  @media(max-width: 414px) {
    margin-top: 43px;
    margin-left: 5px;
  }
}

// links

.link-tel {
  @link-tel-color: #fff;
  @fadeout: 75%;
  display: flex;
  font-size: 1.6em;
  text-decoration: underline;
  margin-left: 1.675em;
  color: @link-tel-color;
  & when (iscolor(@link-tel-color)) {
      text-decoration-color: fadeout(@link-tel-color, @fadeout);
  .transition;
  }
  &:hover {
    color: @color-yellow;
    & when (iscolor(@color-yellow)) {
        text-decoration-color: fadeout(@color-yellow, @fadeout);
    }
  }
  img {
    margin-right: 0.425em;
  }
  @media(max-width: 414px) {
    margin-left: 0;
    margin-top: 30px;
    width: 100%;
    justify-content: center;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 5px;
    padding: 10px;
  }
}

// gerb

.gerb {
  display: flex;
  position: absolute;
  text-decoration: none;
  right: 10px;
  top: 10px;
  span {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 10px;
    text-align: right;
    line-height: 1.1em;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 14px;
    color: #fff;
    .arrow-link {
      width: 10px;
      height: 10px;
      background: url(/img/arrow.svg) no-repeat;
      opacity: 0.5;
      .transition;
    }
    @media(max-width: 414px) {
      font-size: 12px;
      margin-top: 5px;
    }
  }
  &:hover {
    .arrow-link {
      opacity: 1;
      background: url(/img/arrow-hover.svg) no-repeat;
    }
  }
}

// master

.master {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  &::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 450px;
    background: url(../img/dim.png);
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 1;
    right: 25%;
    bottom: -25px;
  }

    &::after {
      content: '';
      position: absolute;
      z-index: 0;
      width: 500px;
      height: 500px;
      background: rgba(255, 255, 255, 0.2);
      border-radius: 50%;
      right: 21%;
      bottom: -100px;
    }
}


// Navigation
.nav {
  display: flex;
  flex-direction: column;
  position: relative;
}

nav {
  position: relative;
  width: 100%;
}

.menu {
  position: relative;
  a.active {
    color: @color-primary;
    border-bottom: 2px solid @color-primary;
  }
}

// Links header

.nav__link {
  color: @color-text-dark;
  &:hover {
    color: @color-primary;
  }
}

.active {
  .nav__link {
    color: @color-primary;
  }
}

.target {
    position: sticky;
    border-bottom: 2px solid transparent;
    z-index: -1;
    transform: translateX(-60px);
}

.nav__link,
.target {
    -webkit-transition: all .35s ease-in-out;
    transition: all .35s ease-in-out;
}

// Links header

.sub-menu {
  display: flex;
  justify-content: space-between;
  &_gray {
    padding: 1em;
    background: @color-background;
  }
}

.menu,
.sub-menu {
  li {
    display: inline-block;
    &:nth-of-type(2n) {
      margin: 0 0.938em;
    }
}


li.sub-list {
  position: relative;
  a {
    padding-left: 15px;
    &:hover {
      span {
        &::before {
          bottom: -4px;
        }
        &::after {
          top: -4px;
        }
      }
    }
  }
  span {
    position: absolute;
    top: 35%;
    display: inline-block;
    width: 10px;
    height: 2px;
    background: @color-primary;
    margin: 2px 0;
    margin-left: -15px;
    &::before,
    &::after {
      content: '';
      position: absolute;
      display: inline-block;
      width: 10px;
      height: 2px;
      background: @color-primary;
    }
    &::before {
      bottom: -3px;
    }
    &::after {
      top: -3px;
    }
  }
}
}
.sub {
    position: absolute;
    top: 10px;
    left: 20%;
    padding: 10px;
    width: 150px;
    border-radius: 5px;
    background: rgb(244, 244, 246);
    box-shadow: 0px 3px 5px 1px #c3c3c3;
    li {
      display: block;
      &:nth-of-type(2n) {
        margin: 0;
      }
    }
}
.nav {
  a {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    &:nth-of-type(2n) {
      margin: 0 0.938em;
    }
    @media (max-width: 768px) {
      font-size: 0.875em;
    }
  }
  span {
    margin-bottom: 0.3em;
    font-size: 0.750em;
    text-transform: uppercase;
    color: @color-text-gray-light;
  }
  @media (max-width: 768px) {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 1.5em;
  }
}

// menu button
.btn-menu {
  display: block;
  height: 30px;
  width: 35px;
  position: relative;
  z-index: 701;
  border-bottom: none;
> span {
  background-color: @color-primary;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 50%;
  transition: height 100ms;
  &:after, &:before {
    content: '';
    background-color: @color-primary;
    height: 5px;
    width: 100%;
    position: absolute;
    transition: all 200ms;
  }
  &:after {
    top: -10px;
  }
  &:before {
    bottom: -10px;
  }
}
&.act {
  > span {
    height: 0;
    &:after, &:before {
      background-color:@color-primary;
      top: 1px;
    }
    &:after {
      transform: rotate(45deg);
    }
    &:before {
      transform: rotate(-45deg);
    }
  }
  }

  @media (min-width: 414px) {
      display: none;
  }
}




.nav_mobile {
position: fixed;
left: 0;
  top: 0;
  box-sizing: border-box;
z-index: 700;
height: 100%;
width: 100%;
  display: none;
opacity: 0;
transition: all .5s ease-out;
&.act {
  display: flex;
  opacity: 1;
  ul li {
  opacity: 1;
}
}
}

.nav_desktop {
  @media (min-width: 414px) {
    display: flex;
    position: relative;
    opacity: 1;
    width: auto;
    margin-top: .3em;
  }
}



