.z-index {
  z-index: 10;
}
main {

}

section {
  display: flex;
  padding: 3em 15px;
  &:nth-child(2n+1) {
    background: #f8f8f8;
  }
  &:nth-last-child(2n+1) {
    background: none;
  }
  h2 {
    margin-bottom: 20px;
    width: 100%;
    text-align: center;
    font-size: 2.4em;
    @media(max-width: 414px) {
      font-size: 1.8em;
    }
  }
  @media(max-width: 414px) {
    padding: 1.5em 15px;
  }
}

section.first {
  padding: 1.5em 15px;
}

section.footer {
  flex-wrap: wrap;
}

.bg-gray {
  background: @color-background;
}

.no-padding {
  padding: 0;
}

// Caption
.caption {
  padding: 1em 2em 2em;
  margin: 1em 0;
  border-radius: 5px;
  legend {
    padding: 10px;
  }
  h1, h2 {
    font-size: 1.875em;
    text-transform: uppercase;
    font-weight: 900;
    margin-bottom: 0;
  }
  p,b {
    padding: 10px;
  }
  &_color {
    border: 2px solid @color-primary;
  }
  &_gray {
    border: 2px solid darken(@color-background,5%);
  }
}

// advantages

.advantages {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  background: #ffffff;
  border: 3px solid darken(#f8f8f8,5%);
  padding: 10px;
  border-radius: 6px;
  @media(max-width: 414px) {
    padding: 0;
  }
  &__item {
    display: flex;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    padding: 15px;
    border-right: 2px solid darken(#f8f8f8,5%);
    &:nth-of-type(3) {
      border: none;
    }
    @media(max-width: 414px) {
      justify-content: flex-start;
      border-right: none;
      border-bottom: 2px solid darken(#f8f8f8,5%);
      br {
        display: none;
      }
    }
    h3 {
      font-size: 1.2em;
      margin: .5em 0 0;
      margin-left: 20px;
      line-height: 1.1em;
    }
    &_title {
      padding-left: 10px;
      padding-right: 20px;
      background: none;
      h3 {
        margin: 0;
        font-size: 1.5em;
      }
    }
    img {
      @media(max-width: 414px) {
        width: 50px;
      }
    }

  }
}

// important

.important {
  width: 100%;
  margin: 10px 0;
  background: #ffffff;
  border: 3px solid darken(#f8f8f8,5%);
  border-radius: 6px;
  &__header {
    padding: 24px 24px 0;
  }
  &__content {
    margin: 12px 24px 24px;
    .block {
      display: flex;
      align-items: center;
      margin-left: 10px;
      @media (max-width: 414px) {
        flex-direction: column;
        align-items: flex-start;
        margin-left: 0;
      }
      &:nth-of-type(2n) {
        margin-top: 15px;
        margin-bottom: 15px;
      }
    }
    p {
      margin-left: 25px;
      @media (max-width: 414px) {
        margin-left: 0;
      }
    }
    .number {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 1;
      color: #fff;
      font-weight: bold;
      @media (max-width: 414px) {
        margin-left: 10px;
        margin-bottom: 10px;
      }
      &::before {
        content: '';
        position: absolute;
        top: -2px;
        display: block;
        width: 25px;
        height: 25px;
        background: #23b55d;
        border-radius: 50%;
        z-index: -1;
      }
    }
  }
  &__type {
    display: flex;
    align-items: center;
    font-size: 18px;
    font-weight: 700;
    &::before {
      @size: 25px;
      content: '';
      display: inline-block;
      width: @size;
      height: @size;
      margin-right: 10px;
      margin-bottom: 5px;
    }
  }
  &__warn {
    .important__type {
      &::before {
        background: url('/img/exclamation-triangle.svg') no-repeat;
      }
    }
  }
  &__info {
    .important__type {
      @media (max-width: 414px) {
        flex-direction: column;
        align-items: flex-start;
      }
      &::before {
        background: url('/img/info-circle.svg') no-repeat;
      }
    }
  }
}

// price

.items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  background: #fff;
  border: 3px solid #ebebeb;
  border-radius: 6px;
  @media (max-width: 414px) {
    padding: 0;
  }
  .item {
    display: flex;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    padding: 15px;
    background: #ffffff;
    @media (max-width: 414px) {
      align-items: flex-end;
      padding: 25px 15px;
    }
    &:nth-child(2n) {
      border-right: 2px solid #ebebeb;
      border-left: 2px solid #ebebeb;
      border-top: none;
      border-bottom: none;
      @media (max-width: 414px) {
        border-right: none;
        border-left: none;
        border-top: 2px solid #ebebeb;
        border-bottom: 2px solid #ebebeb;
      }
    }
    &__text {
      text-align: right;
      line-height: 1em;
      margin-right: 20px;
      h3 {
        font-size: 2.4em;
        margin: 0;
      }
      span.description {
        color: #a3a3a3;
        &__label {
          padding: 3px 6px;
          background: #ffb732;
          color: #ffffff;
          border-radius: 3px;
        }
      }
      span.price {
        font-size: 1.5em;
        font-weight: bold;
      }
    }
    &__svg {
      @media (max-width: 414px) {
        img {
          max-width: 100px;
        }
      }
    }
  }
}

.description {
  width: 100%;
  text-align: center;
  margin-top: 20px;
  color: #a3a3a3;
  font-size: 14px;
}

section.map {
  padding: 0;
}

#map {
  height: 450px;
  width: 100%;
}

.slider {
  position: relative;
  @media (max-width: 414px) {
      padding: 0;
  }
}

@media (max-width: 414px) {
  #vk_community_messages {
    display: none;
  }
}
