input[type='text'],
textarea,
select,
input[type='password']{ /*  */
  box-sizing: border-box;
  outline: none;
  width: 100%;
  font-size: 18px;
  line-height: 20px;
  font-family: @base-font;

  &:focus{

  }
}

label{
  cursor: pointer;
}

button{
  cursor: pointer;
  outline: none;
}

CSS

textarea,
input.text,
input[type="text"],
input[type="number"],
input[type="tel"],
input[type="button"],
input[type="submit"],
.input-checkbox {
-webkit-appearance: none;
border-radius: 0;
}